*{
padding: 0;
margin: 0;
box-sizing: border-box;

}

html, body {
  background-color: #ccc;
  }
  
.app{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  padding-top: 15px;
  font-family: Lato,sans-serif;
}  
.contenedor-logo{
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  margin: 0px;
}
.logo{
height: 50px;
margin: 40px;
}

.contenedor-contador{
  align-items: center;
  justify-content: center;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;

}


button{
    width: 250px;
    height: 80px;
    font-family: roboto,sans-serif;
    font-weight: bold;
    color: white;
    border: 2px solid white;
    border-radius: 15px;
    margin: 5px;
    cursor: pointer;
    font-size: 20px;
  }
  button:hover{
    background-color: #ccc;
  }
  .boton-click{
    background-color: rgb(111, 0, 0);
  }
  .boton-reiniciar{
    background-color: rgb(11, 11, 11);
  }

.contador{
    align-items: center;
    justify-content: center;
    display: flex;
    height: 5em;
    min-width: 5em;
    margin-bottom: 3rem;
    font-size: 5em;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: bold;
    background-color: white;
    border: 0.3rem solid black;
}
